

.homeContainer{
    display: flex;
    align-items: center;
    height: 100%;
}
.intro{
   
    color: #fff;
    min-width: 360px;
    order: 0;
    width: 60%;
    
}

.image{
    order: 1;
    width: 40%;
}


.intro h1{
    font-size: 78px;
    line-height: 0.2;
    text-shadow: 2px 3px 6px #00C6CF;
}

.intro h2,h3{
    text-shadow: 0.5px 2px 3px #00C6CF;
}



.button{
   
    color:#00C6CF !important;
    border-color: #00C6CF !important;
}

.intro h2{
    font-size: 40px;
    line-height: 0.5;
}

.typingText {
    width: 28ch;
    animation: typing 3.5s steps(28), blink .5s step-end infinite alternate;
    white-space: nowrap;
    overflow: hidden;
    border-right: 3px solid;
    animation-iteration-count:infinite;
    font-size: 2em;
    font-family: monospace;
    
    
  }
  
  @keyframes typing {
    from {
      width: 0ch
    }
  }

 
      
  @keyframes blink {
    50% {
      border-color: transparent
    }
  }



@media only screen and (max-width: 600px) {

   
    .intro h1{
        font-size: 45px;
    }

    .typingText{
        font-size: 1em;
    }
}

@media only screen and (max-width: 992px) {
   
    .intro{
       
        width: 100%;
   
    }
    .image{
        width: 100%;
    }

}